import SiteExport from "../../site-export/site_export.json";

export interface Recipe {
    id: string;
    resultItem: string;
    resultCount: number
}

export interface CraftingRecipe extends Recipe {
    shapeless: boolean;
    ingredients: string[][];
}

export interface SmeltingRecipe extends Recipe {
    ingredient: string[];
}

export interface InscriberRecipe extends Recipe {
    top: string[];
    middle: string[];
    bottom: string[];
}

export interface ItemInfo {
    id: string;
    displayName: string;
    tooltipLines: string[];
    icon: string;
}

export const craftingRecipes: Record<string, CraftingRecipe> = SiteExport.craftingRecipes;
export const smeltingRecipes: Record<string, SmeltingRecipe> = SiteExport.smeltingRecipes;
export const inscriberRecipes: Record<string, InscriberRecipe> = SiteExport.inscriberRecipes;

const indexByItemId = new Map<string, ItemInfo>(
    Object.values(SiteExport.items).map(item => [item.id, {
        ...item,
        icon: item.icon.replaceAll('\\', '/').replaceAll(/^icons\//g, '')
    }])
);

export function getItemInfo(itemId: string): ItemInfo {
    const entry = indexByItemId.get(itemId);
    if (!entry) {
        throw new Error(`Missing item-info for '${itemId}'.`);
    }
    return entry;
}
