import React from 'react';
import {getItemInfo} from "./SiteExport";
import {graphql, useStaticQuery} from "gatsby";
import Tooltip from "./Tooltip";
import ItemLink from "./ItemLink";

export interface ItemIconProps {
    itemId: string;
}

function ItemIcon({itemId}: ItemIconProps) {
    const {allFile} = useStaticQuery(graphql`
    {
      allFile(filter: { sourceInstanceName: { eq: "itemIcons" } }) {
        edges {
          node {
            relativePath
            publicURL
          }
        }
      }
    }
  `);
    const itemInfo = getItemInfo(itemId);
    const iconUrl = allFile.edges.find((e: any) => e.node.relativePath === itemInfo.icon)?.node?.publicURL;
    if (!iconUrl) {
        throw new Error("Couldn't find icon for item: " + itemInfo.icon);
    }

    return <Tooltip lines={itemInfo.tooltipLines}>
        <ItemLink id={itemId}>
            <img src={iconUrl} alt={itemInfo.displayName} title={itemInfo.displayName} className="item-icon"/>
        </ItemLink>
    </Tooltip>;
}

export default ItemIcon;
