import React, {ReactNode} from 'react';
import {getItemInfo} from "./SiteExport";
import {graphql, Link, useStaticQuery} from "gatsby";

export interface ItemLinkProps {
    id: string;
    children: ReactNode | undefined;
}

function ItemLink({id, children}: ItemLinkProps) {
    const data = useStaticQuery(graphql`query GetItemId {
  allMdx {
    edges {
      node {
        slug
        frontmatter {
          item_ids
        }
      }
    }
  }
}
`);
    let pageUrl: string | undefined;
    if (id.startsWith("appliedenergistics2:")) {
        for (const {node} of data.allMdx.edges) {
            if (node.frontmatter.item_ids?.includes(id)) {
                pageUrl = node.slug;
                break;
            }
        }

        if (!pageUrl) {
            throw new Error("No page found for " + id);
        }
    }

    const itemInfo = getItemInfo(id);

    if (!children) {
        children = itemInfo.displayName;
    }

    if (!pageUrl) {
        return <span>{children}</span>;
    } else {
        return (
            <Link to={'/' + pageUrl}>{children}</Link>
        );
    }
}

export default ItemLink;
